// ==========================================================================
// Form Elements
// ==========================================================================

// Search Form

.search-form {
    display: flex;
    padding-top: 10px;
    outline: none;

    &-btn {
        @extend .search-bar;
        width: 20%;
        outline: none;
    }
  }
  .form-control {
    height: 47px;
    width: 100%;
    
  }
  .search-bar {
    display: inline-block;
    background-color: rgba(0,0,0,.1);
    outline: rgba(0,0,0,.1);
    
    border: 2px solid rgba(0,0,0,.1);
  }




// Form Checkbox

.label-checkbox {
    display:inline-block;
    cursor:pointer;
    width: 32px;
    height: 32px;
    background-color: palette(grey, xx-light);
    border: 3px solid palette(grey, x-dark);
    border-radius: 10%;
    margin-top: em(20px);
    margin-right: em(20px);
    margin-left: em(20px);

    &::before {
        content: "";
        color: $white;
        display:block;
        position: center;
       // top:50%;
        //left:50%;
        transform:translate(50%, -20%);
        width:0;
        height:0;
        border-top: 0;
       // border-bottom: 3px solid #fff;
        //border-left: 3px solid #fff;
        //color: #fff;
        //font-size: 20px;
    }
  }

  input[type="checkbox"] {
    display: none;

    &:checked + .label-checkbox {
        background-color: palette(grey, base);
        border: 3px solid palette(grey, base);
        

        &::before {
            scale: 2;
            width: 10px;
            height: 10px;
            position: center;
            content: "\2713";
        }
        
        
    }
  }